import { createContext, useContext, useMemo, useState, ReactNode } from "react";
import {
  createTheme,
  CssBaseline,
  GlobalStyles,
  ThemeProvider,
  PaletteMode,
  responsiveFontSizes
} from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";

const primaryFont = "'JetBrains Mono', 'Segoe UI', sans-serif";

const getDesignTokens = (mode: PaletteMode) => ({
  palette: {
    mode,
    ...(mode === "light"
      ? {
          primary: { main: "#0066ff" },
          secondary: { main: "#00d2ff" },
          background: { default: "#f4f7fb", paper: "#ffffff" },
          text: { primary: "#0b1020", secondary: "#405674" }
        }
      : {
          primary: { main: "#4d8dff" },
          secondary: { main: "#00f2ff" },
          background: { default: "#0b1020", paper: "#121826" },
          text: { primary: "#f5f8ff", secondary: "#a9b5d8" }
        }),
    success: { main: "#4caf50" },
    warning: { main: "#ff9800" },
    error: { main: "#f44336" },
    info: { main: "#00bcd4" }
  },
  typography: {
    fontFamily: primaryFont,
    h1: { fontWeight: 700 },
    h2: { fontWeight: 700 },
    h3: { fontWeight: 600 },
    h4: { fontWeight: 600 },
    h5: { fontWeight: 500 },
    h6: { fontWeight: 500 },
    button: { textTransform: "none" as const, fontWeight: 600 }
  },
  shape: { borderRadius: 16 }
});

type ColorModeContextType = {
  mode: PaletteMode;
  toggleColorMode: () => void;
};

const ColorModeContext = createContext<ColorModeContextType | undefined>(undefined);

export const useColorMode = () => {
  const context = useContext(ColorModeContext);
  if (!context) {
    throw new Error("useColorMode must be used within a SentinelThemeProvider");
  }
  return context;
};

export const SentinelThemeProvider = ({ children }: { children: ReactNode }) => {
  const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
  const [mode, setMode] = useState<PaletteMode>(prefersDarkMode ? "dark" : "light");

  const colorMode = useMemo(
    () => ({
      mode,
      toggleColorMode: () => {
        setMode((prevMode) => (prevMode === "light" ? "dark" : "light"));
      }
    }),
    [mode]
  );

  const theme = useMemo(() => responsiveFontSizes(createTheme(getDesignTokens(mode))), [mode]);

  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <GlobalStyles
          styles={{
            "*": {
              boxSizing: "border-box"
            },
            html: {
              height: "100%",
              scrollBehavior: "smooth",
              backgroundColor: theme.palette.background.default
            },
            body: {
              height: "100%",
              margin: 0,
              backgroundImage:
                mode === "dark"
                  ? "radial-gradient(circle at 20% 20%, rgba(0, 255, 255, 0.05), transparent 50%), radial-gradient(circle at 80% 0%, rgba(0, 102, 255, 0.06), transparent 45%)"
                  : "radial-gradient(circle at 0% 25%, rgba(0, 102, 255, 0.08), transparent 55%)",
              backgroundRepeat: "no-repeat",
              minHeight: "100vh"
            },
            "#root": {
              height: "100%"
            }
          }}
        />
        {children}
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
};

