import { Component, ReactNode } from "react";
import { Alert, AlertTitle, Box, Button } from "@mui/material";
import ReportProblemIcon from "@mui/icons-material/ReportProblem";

type ErrorBoundaryProps = {
  children: ReactNode;
};

type ErrorBoundaryState = {
  hasError: boolean;
};

class ErrorBoundary extends Component<ErrorBoundaryProps, ErrorBoundaryState> {
  constructor(props: ErrorBoundaryProps) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  componentDidCatch(error: Error, errorInfo: unknown) {
    // eslint-disable-next-line no-console
    console.error("Uncaught error in Sentinel:", error, errorInfo);
  }

  handleReload = () => {
    this.setState({ hasError: false });
    window.location.reload();
  };

  render() {
    if (this.state.hasError) {
      return (
        <Box display="grid" minHeight="100vh" p={3} sx={{ placeItems: "center" }}>
          <Alert
            icon={<ReportProblemIcon fontSize="large" />}
            severity="error"
            variant="outlined"
            sx={{ maxWidth: 480 }}
          >
            <AlertTitle>Un imprvu sest produit</AlertTitle>
            Une erreur empche laffichage du tableau de bord. Ressaie dans un instant.
            <Button sx={{ mt: 2 }} variant="contained" color="primary" onClick={this.handleReload}>
              Recharger la page
            </Button>
          </Alert>
        </Box>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;

