import { NavLink } from "react-router-dom";
import {
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Drawer,
  Toolbar,
  useTheme,
  Tooltip,
  Box
} from "@mui/material";
import DashboardIcon from "@mui/icons-material/Speed";
import GroupIcon from "@mui/icons-material/Groups2";
import InsightsIcon from "@mui/icons-material/Insights";
import WarningIcon from "@mui/icons-material/Warning";

const navItems = [
  { to: "/", label: "Accueil", icon: <DashboardIcon /> },
  { to: "/players", label: "Joueurs", icon: <GroupIcon /> },
  { to: "/alerts", label: "Alertes", icon: <WarningIcon /> },
  { to: "/analytics", label: "Analyses", icon: <InsightsIcon /> }
];

type SidebarProps = {
  width?: number;
};

const Sidebar = ({ width = 256 }: SidebarProps) => {
  const theme = useTheme();

  return (
    <Drawer
      variant="permanent"
      sx={{
        width,
        flexShrink: 0,
        [`& .MuiDrawer-paper`]: {
          width,
          boxSizing: "border-box",
          borderRight: `1px solid ${theme.palette.divider}`,
          backgroundImage:
            "linear-gradient(180deg, rgba(9, 14, 24, 0.75) 0%, rgba(18, 22, 38, 0.95) 75%)"
        }
      }}
    >
      <Toolbar />
      <Box sx={{ px: 2, pt: 1, color: theme.palette.text.secondary, fontSize: 12, letterSpacing: 1.2 }}>
        NAVIGATION
      </Box>
      <List sx={{ py: 0 }}>
        {navItems.map((item) => (
          <NavLink key={item.to} to={item.to} end={item.to === "/"} style={{ textDecoration: "none" }}>
            {({ isActive }) => (
              <ListItemButton
                sx={{
                  margin: "4px 12px",
                  borderRadius: 12,
                  color: isActive ? theme.palette.primary.contrastText : theme.palette.text.secondary,
                  backgroundColor: isActive ? `${theme.palette.primary.main}33` : "transparent",
                  "&:hover": {
                    backgroundColor: `${theme.palette.primary.main}22`
                  }
                }}
              >
                <Tooltip title={item.label} placement="right">
                  <ListItemIcon
                    sx={{
                      color: "inherit",
                      minWidth: 40
                    }}
                  >
                    {item.icon}
                  </ListItemIcon>
                </Tooltip>
                <ListItemText primary={item.label} />
              </ListItemButton>
            )}
          </NavLink>
        ))}
      </List>
    </Drawer>
  );
};

export default Sidebar;

