import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Stack,
  Tooltip,
  Avatar,
  InputAdornment,
  TextField
} from "@mui/material";
import NotificationsIcon from "@mui/icons-material/Notifications";
import LightModeIcon from "@mui/icons-material/LightMode";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import SearchIcon from "@mui/icons-material/Search";
import ShieldIcon from "@mui/icons-material/Shield";

import { useColorMode } from "../../styles/theme";

type HeaderProps = {
  onSearch?: (value: string) => void;
};

const Header = ({ onSearch }: HeaderProps) => {
  const { mode, toggleColorMode } = useColorMode();

  return (
    <AppBar
      position="sticky"
      color="transparent"
      elevation={0}
      sx={{
        backdropFilter: "blur(16px)",
        backgroundColor: (theme) => theme.palette.background.paper + "f2",
        borderBottom: (theme) => `1px solid ${theme.palette.divider}`
      }}
    >
      <Toolbar sx={{ gap: 2 }}>
        <ShieldIcon color="primary" fontSize="large" />
        <Typography variant="h6" fontWeight={700} sx={{ flexShrink: 0 }}>
          Tableau de bord scurit
        </Typography>
        <TextField
          placeholder="Rechercher un joueur, une alerte ou une zone"
          size="small"
          sx={{ flex: 1, maxWidth: 420 }}
          onChange={(event) => onSearch?.(event.target.value)}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon color="action" />
              </InputAdornment>
            )
          }}
        />
        <Stack direction="row" spacing={1} alignItems="center">
          <Tooltip title="Changer de thme">
            <IconButton onClick={toggleColorMode} color="primary">
              {mode === "dark" ? <LightModeIcon /> : <DarkModeIcon />}
            </IconButton>
          </Tooltip>
          <Tooltip title="Alertes en direct">
            <IconButton color="primary">
              <NotificationsIcon />
            </IconButton>
          </Tooltip>
          <Avatar
            alt="Analyste scurit"
            src="https://i.pravatar.cc/150?img=47"
            sx={{ border: (theme) => `2px solid ${theme.palette.primary.main}` }}
          />
        </Stack>
      </Toolbar>
    </AppBar>
  );
};

export default Header;

